.popup__title{
    max-width: 330px;
    font-family: 'Inter', "Arial ", sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    margin: 0;
    margin-top: 34px;
    margin-left: 36px;
    margin-bottom: 54px;
}
@media screen and (max-width: 910px){
    .popup__title{
        font-size: 18px;
        line-height: 22px;
        margin-left: 22px;
        margin-right: 22px;
    }
}